import React from 'react'
import './Header.scss'
import { Button } from 'antd'

const Header = props => {
  return (
    <div className="header-box">
      <div className="user-content">
        {props.data?.patientName ? (
          <div className="patient-detail">
            <span>{props.data.patientName}</span>
            <span>{props.data.gender_dictText}</span>
            <span>{props.data.age}</span>
            <span>{props.data.medicalCaseCode}</span>
            <span>patientId{props.data.patientId}</span>
            <span>{props.data.createTime}</span>
          </div>
        ) : null}
      </div>
      {props.pageType === 'review' ? (
        <div className="export">
          <Button disabled={props.pageState === 'admin'} type="primary" onClick={props.handleShowModal}>
            
          </Button>
        </div>
      ) : null}
    </div>
  )
}

export default Header
